.email {
    background: #E5C643;
}

.email__blok {
    padding: 140px 0 150px 0;
    text-align: center;
}

.email__title {
    color: #FFF;
    font-size: 55px;
    font-style: normal;
    font-weight: 900;
    line-height: 70px;
    letter-spacing: 1.65px;
    max-width: 960px;
    display: inline-block;
}

.email__desc {
    color: #FFFCF8;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 35px;
    letter-spacing: 0.96px;
    padding-top: 37px;
    padding-bottom: 50px;
}

.email__input {
    width: 580px;
    background-color: #FFF;
    border-radius: 10px;
    padding: 27px 0 27px 27px;

}

form {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
}




.email__input::placeholder {
    color: #767676;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 35px;
    letter-spacing: 0.72px;

}


.email__form {
    color: #FFF;
    font-family: Poppins;
    font-size: 33px;
    font-style: normal;
    font-weight: 500;
    line-height: 35px;
    letter-spacing: 0.99px;
    text-transform: capitalize;
    padding: 15px 35px;
    background-color: #000;
    border-radius: 8px;
}