.download {
    padding: 180px 0;
}

.downliad__blok {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 300px;
}

.download__title {
    font-size: 48px;
    font-style: normal;
    font-weight: 900;
    line-height: 150%;
    letter-spacing: 2.88px;
    max-width: 510px;
}

.download__desc {
    color: #7C7C7C;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 35px;
    letter-spacing: -0.96px;
    max-width: 420px;

    padding-top: 40px;
    padding-bottom: 60px;
}

.download__img:hover {
    animation: download 0.7s ease-in-out;
}

@keyframes download {
    0% {
        transform: scale(1.1);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
    }
}




.download__wrraper-btn {
    display: flex;
    align-items: center;
    column-gap: 20px;
}