.card {
    position: relative;
    cursor: pointer;
}

.card:hover .card__img {
    animation: opacity 0.2s ease-in-out;
}

.card:hover .card__icon {
    animation: slide 0.5s ease-in-out;
}

@keyframes opacity {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0.8;
    }

    100% {
        opacity: 1;
    }
}

@keyframes slide {
    0% {
        left: 0;
    }

    50% {
        left: -40px;
    }

    100% {
        left: 0;
    }
}

.card__link {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.card__img {
    margin-bottom: 28px;
    border-radius: 20px;
}

.card__body {
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
    align-items: center;
}

.card__title {
    color: #000;
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 50px;
    letter-spacing: -1.28px;
    margin-bottom: 7px;
}

.card__muted {
    color: #7F7F7F;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 50px;
    letter-spacing: -0.96px;
}

.card__icon {
    position: relative;
}