.promo {
    margin-bottom: 63px;
}

.promo__content {
    border-radius: 59px;
    background-color: #F4F6F5;

    display: flex;
    column-gap: 24px;

    padding-top: 68px;
    padding-left: 120px;
}


.promo__title {
    color: var(--textColor);
    font-size: 96px;
    font-weight: 900;
    line-height: 120px;
    margin-bottom: 40px;
    max-width: 480px;
}

.promo__desc {
    color: var(--textColor);
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -1.6px;
    margin-bottom: 32px;
}

.promo__btn-wrapper {
    padding-bottom: 90px;
    display: flex;
    column-gap: 34px;
    align-items: center;
}

.promo__btn-wrapper::before {
    content: ' ';
    display: inline-block;
    width: 190px;
    height: 32px;
    background-image: url('./../../img/icons/decor-yellow.svg');
}

.promo__btn {
    border-radius: 10px;
    padding: 18px 40px;
    color: #FFF;
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -1.5px;
    background: black;

}

.highlight {
    position: relative;
    display: inline-block;
}

.highlight span {
    position: relative;
}

.highlight::before {
    content: ' ';
    display: block;
    position: absolute;
    top: -12px;
    bottom: 0px;
    left: -40px;
    right: -160px;
    background-color: #FFF;
    transform: rotate(-2deg);
}

.highlight--yellow::before {
    right: -110px;
    background-color: #EBD96B;

}