:root {
    --textColor: #000;
    --yellow: #EBD96B;
}

body {
    font-family: 'Poppins', sans-serif;
    color: var(--textColor);
}

.container {
    max-width: 1780px;
    margin: 0 auto;
    padding: 0 30px;
}

.title-2 {
    display: inline-block;

    font-size: 48px;
    font-weight: 900;
    line-height: 50px;
    letter-spacing: 2.88px;
    position: relative;
}

.title-2::after {
    content: ' ';
    position: absolute;
    z-index: -1;
    background-image: url('./../img/icons/decor-yellow.svg');
    background-repeat: no-repeat;
    right: 0;
    bottom: 0;
    width: 190px;
    height: 32px;
}