.footer {
    background-color: black;
}

.footer__blok {
    padding-top: 120px;
    padding-bottom: 150px;
    display: flex;
    justify-content: space-between;
}

.footer__logo {
    color: #FFF;
    font-size: 40px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    letter-spacing: 1.2px;
}

.footer__desc {
    color: #8E8E8E;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0.4px;
    padding: 32px 0;
    max-width: 415px;
}

.footer__icon-list {
    display: flex;
    align-items: center;
    column-gap: 15px;
}

.footer__row-list {
    display: flex;
    column-gap: 150px;
}

.list__item {
    padding-bottom: 32px;
}

.list__item a {
    color: #8E8E8E;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0.4px;

}

.list__item.active a {
    color: #D9D9D9;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0.4px;

}