.promoSale {
    background: linear-gradient(360deg, #E0C340 0%, #DFC23E 3.93%, #E1C441 7.23%, #E3C743 10.23%, #E4C542 13.14%, #E6C744 16.22%, #E7C845 19.84%, #E5C643 24.38%, #E6C945 31.00%, #E3C743 37.19%, #E9CA48 43.33%, #EDCE49 50.36%, #F0D44C 56.57%, #F4D84F 62.71%, #F6DA52 67.12%, #F7DB53 73.77%, #F9DD55 79.08%, #F9DF56 85.64%, #FAE157 93.50%, #F9DF56 100%);
}




.promoSale__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 64px;

}

.promoSale__title {
    color: #000;
    font-size: 112px;
    font-weight: 900;
    line-height: 137px;
    max-width: 600px;
}



.promoSale__desc {
    color: #231300;
    font-size: 36px;
    font-weight: 500;
    line-height: 52px;
    letter-spacing: -0.721px;
    padding-top: 30px;
    padding-bottom: 90px;
    max-width: 640px;
}

.promoSale__content {
    color: #000;
    font-size: 36px;
    font-weight: 400;
    line-height: 52px;
    letter-spacing: -0.721px;
}

.promoSale__strong {
    color: #000;
    font-size: 36px;
    font-weight: 700;
    line-height: 52px;
    letter-spacing: 0.721px;
}


.promoSale__btn-wrapper {
    padding-top: 50px;
    display: flex;
    column-gap: 34px;
    align-items: center;
}

.promoSale__btn {
    border-radius: 10px;
    padding: 18px 40px;
    color: #FFF;
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -1.5px;
    background: black;
}


.light {
    position: relative;
    display: inline-block;
}

.light span {
    position: relative;
}

.light::before {
    content: ' ';
    display: block;
    position: absolute;
    top: -12px;
    bottom: 0px;
    left: -30px;
    right: -30px;
    background-color: #FFF;
    transform: rotate(-2deg);
}